@import url(https://fonts.googleapis.com/css?family=Work+Sans:300,600);
:root {
  --nav-background: rgba(0, 214, 170, .85);
  --main-background: rgba(40, 44, 52, 1);
}

*, *::before, *::after {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'Work Sans', sans-serif;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.intro {
  font-size: calc(10px + 2vmin);
  color: white;
}

.text-appear {
  -webkit-animation: appear 2000ms ease-in-out 1;
          animation: appear 2000ms ease-in-out 1;
}

.text-glow {
  -webkit-animation: glow 1500ms linear 1 1000ms;
          animation: glow 1500ms linear 1 1000ms;
}

@-webkit-keyframes glow {
  40% {
    text-shadow: 0 0 10px #fff;
  }
}

@keyframes glow {
  40% {
    text-shadow: 0 0 10px #fff;
  }
}

@-webkit-keyframes appear {
  from { color: rgba(255, 255, 255, 0); }
  to { color: rgba(255, 255, 255, 1); }
}

@keyframes appear {
  from { color: rgba(255, 255, 255, 0); }
  to { color: rgba(255, 255, 255, 1); }
}
.home {
  height: 100vh;
  display: grid;
  place-items: center;
}
.skills {
  height: 100vh;
  display: grid;
  place-items: center;
}

.experience {
  height: 100vh;
  display: grid;
  place-items: center;
}

.projects {
  height: 100vh;
  display: grid;
  place-items: center;
}

header {
  background: var(--nav-background);
  text-align: center;
  position:fixed;
  z-index: 999;
  width: 100%;
}

.logo {
  color: var(--main-background);
}

nav {
  position: absolute;
  text-align: left;
  top: 100%;
  left: 0;
  background: var(--nav-background);
  width: 100%;
  -webkit-transform: scale(1, 0);
          transform: scale(1, 0);
  -webkit-transform-origin: top;
          transform-origin: top;
}

nav ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

nav li {
  margin-bottom: 1rem;
  margin-left: 1rem;
}

nav a {
  color: white;
  text-decoration: none;
  font-size: 1.2rem;
  text-transform: uppercase;
  opacity: 0;
  transition: opacity 150ms ease-in-out;
}

nav a:hover {
  color: black;
}

.nav-toggle {
  display: none;
}

.nav-toggle:checked ~ nav {
  -webkit-transform: scale(1, 1);
          transform: scale(1, 1);
  transition: -webkit-transform 400ms ease-in-out;
  transition: transform 400ms ease-in-out;
  transition: transform 400ms ease-in-out, -webkit-transform 400ms ease-in-out;
}

.nav-toggle:checked ~ nav a {
  opacity: 1;
  transition: opacity 250ms ease-in-out 250ms;
}

.nav-toggle-label {
  position: absolute;
  top: 0;
  left: 0;
  margin-left: 1rem;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.nav-toggle-label span,
.nav-toggle-label span::before,
.nav-toggle-label span::after {
  display: block;
  background: white;
  height: 2px;
  width: 2rem;
  border-radius: 2px;
  position: relative;
}

.nav-toggle-label span::before,
.nav-toggle-label span::after {
  content: '';
  position: absolute;
}

.nav-toggle-label span::before {
  bottom: 7px;
}

.nav-toggle-label span::after {
  top: 7px;
}

@media screen and (min-width: 850px) {
  .nav-toggle-label {
    display: none;
  }

  header {
    display: grid;
    grid-template-columns: 1fr auto minmax(600px, 3fr) 1fr;
  }
  
  .logo {
    grid-column: 2 / 3;
  }
  
  nav {
    
    position: relative;
    text-align: left;
    transition: none;
    -webkit-transform: scale(1,1);
            transform: scale(1,1);
    background: none;
    top: auto;
    top: initial;
    left: auto;
    left: initial;
    
    grid-column: 3 / 4;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    -webkit-align-items: center;
            align-items: center;
  }
  
  nav ul {
    display: -webkit-flex;
    display: flex;
  }
  
  nav li {
    margin-left: 0;
    margin-bottom: 0;
  }
  
  nav a {
    opacity: 1;
    position: relative;
    margin-left: 3rem;
  }
  
  nav a::before {
    content: '';
    display: block;
    height: 5px;
    background: black;
    position: absolute;
    top: -.75em;
    left: 0;
    right: 0;
    -webkit-transform: scale(0, 1);
            transform: scale(0, 1);
    transition: -webkit-transform ease-in-out 250ms;
    transition: transform ease-in-out 250ms;
    transition: transform ease-in-out 250ms, -webkit-transform ease-in-out 250ms;
  }
  
  nav a:hover::before {
    -webkit-transform: scale(1,1);
            transform: scale(1,1);
  }
}
